import React from "react";
import "./Section1.css"
import food from "../../images/main-image.jpg"

var c=1
var arr=["Movie marathon?","Game night?","Late night at office?","Unexpected guests?","Cooking gone wrong?","Hungry?"]
window.onload=()=>{    
setInterval(()=>{
        document.getElementById("nav-head").innerHTML=arr[c]
        c++
        if(c===6)
        c=0
        console.log(c)
    },4000)
}
const Section1 = () => {
  return (
    <>
  <div className="container">
    <div className="section1">
      <div className="sub_section1">
    <img
          src="data:image/png;base64,"
          alt=""
        />
           <div className="buttons_login_signup">
            <button><b>Login</b></button>
            <button>Sign Up</button>
             </div>
        </div>
        <div className="sub_section2">
      <div className="headings">
      <marquee behavior="scroll" direction="up" scrollamount="3.8">
      <h1 id="nav-head">Unexpected Guests?</h1>
      </marquee>
      <h2>Order food from favourite restaurants near you.</h2>
      <div >
      <label className="inputbutton">
      <input type="text" placeholder="Enter your Delivery Location" />
      <button>Locate Me</button>
      </label>
      <button className="find_food">Find Food</button>
      </div>
      <h3>Popular cities in India</h3>
      <div className="cities">
       <ul>
      <li> <a href="#" className="black">Ahmedabad</a></li>
      <li> <a href="#" className="grey">Bangalore</a></li>
      <li> <a href="#" className="black">Chennai</a></li>
      <li> <a href="#" className="grey">Delhi</a></li>
      <li> <a href="#" className="black">Gurgaon</a></li>
      <li> <a href="#" className="grey">Hyderabad</a></li>
      <li> <a href="#" className="black">Mumbai</a></li>
      <li> <a href="#" className="grey">Kolkata</a></li>
     

   
      <li> <a href="#" className="black">Pune</a></li>
   
      <li> <a href="#" className="grey"> &amp; more</a></li>
      </ul>
      </div>
     
      </div>
    </div>
    </div>
   
    <div className="section2">
    <img src={food}></img>
    </div>
  </div>
    </>
  );
};

export default Section1;
